.footer{
	position:absolute;
	bottom:0px;
	width:100%;
	height:260px;
	color:#FFFFFF;
	background-color:lightseagreen;
}

#footer-row{
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 40% 20px 40%;
	padding: 10px;
}

.vl{
	border-left:4px solid #ffffff;
	height: 230px;
	margin-left:auto;
	margin-right:auto;
}

#footer-row-info{
	
	width:60%;
	height:230px;
	color:#000000;
	text-align: left;
	font-size: 15px;
	margin-left:auto;
	margin-right:auto;
}

.footer input[type=text], textarea{
	width: 90%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	resize: vertical;
}

.footer input[type=submit]{
	color: black;
}


